import "assets/scss/login.scss";

import { LoginFlow } from "@ory/kratos-client";
import { Button, Col, Form, Input, message, Row, Spin, Typography } from "antd";
import logo from "assets/img/logo192.png";
import config from "config/kratos";
import React, { useCallback, useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import axios from "axios";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};

const RecoveryNew = () => {
  const [requestResponse, setRequestResponse] = useState<LoginFlow>();
  const [loading, setLoading] = useState<boolean>(false);
  useEffect(() => {
    document.title = "Register | Courses | AnC";
  });
  return (
    <div className="auth">
      <div className="background">
        <Row>
          <Col span={18}>
            <div className="header">
              <img src={logo} />
              <h1>Academics and Career Council</h1>
              <h4>Courses and Reviews Portal</h4>
            </div>
          </Col>
          <Col span={6}>
            <div id="login-password" className="form">
              <React.Fragment>
                <div className="fieldset">
                  <Spin tip={"Loading..."} spinning={loading}>
                    <Form
                      {...layout}
                      initialValues={{ remember: true }}
                      onFinish={(v) => {
                        setLoading(true);
                        var req = new FormData();
                        req.append("username", v.username);
                        axios
                          .post(config.recover, req)
                          .then(() => {
                            setLoading(false);
                            message.success("Recovery Email Sent!");
                          })
                          .catch(() => {
                            setLoading(false);
                            message.error(
                              "We encountered an error! Please check your username and try again!"
                            );
                          });
                      }}
                      layout="vertical"
                      className="fieldset"
                    >
                      <Form.Item style={{ marginBottom: 0 }}>
                        <Typography>
                          <Typography.Title level={2} className="formtitle">
                            Recovery
                          </Typography.Title>
                        </Typography>
                      </Form.Item>
                      <Form.Item>
                        <Form.Item
                          label={"IITK Username"}
                          style={{ marginBottom: 0 }}
                          name="username"
                        >
                          <Input
                            name="username"
                            required
                            style={{ maxWidth: "100%", width: "100%" }}
                          ></Input>
                        </Form.Item>
                      </Form.Item>
                      <Form.Item style={{ marginBottom: 0 }}>
                        <NavLink
                          className="login-form-forgot"
                          to={config.routes.recovery.path}
                        >
                          Sign in?
                        </NavLink>
                      </Form.Item>

                      <Form.Item style={{ marginBottom: 2, paddingBottom: 20 }}>
                        <Button
                          type="primary"
                          className="login-form-button"
                          style={{ width: "100%" }}
                          htmlType="submit"
                        >
                          {"Recover"}
                        </Button>
                      </Form.Item>
                    </Form>
                  </Spin>
                </div>
              </React.Fragment>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default RecoveryNew;
